const util = require('../../components/zwy/util/util.js');
import clickCircle from '../../components/zwy/clickCircle.vue';
import moment from 'moment'


export default {
	components: {
		clickCircle
	},
	data() {
		return {
			roomLog: {},
			userInfo: {},
			isX: 999, //放在store统一管理
			isY: 999, //放在store统一管理
			toView: '',
			showPicker: true,
			pickRoomList: [],
			chooseRoom: "",
			MsgInfo: {}


		}
	},
	onShow() {

		console.log(moment().format('YYYY-MM-DD HH:mm:ss'));
		this.isLogin()

		if (!this.userInfo.realNameInfo) {

			uni.showModal({
				title: "请先实名",
				showCancel: false,
				success: function(res) {
					if (res.confirm) {
						console.log("确定了啊")
						uni.switchTab({
							url: "/pages/profile/profile"
						})
					}

				}
			})
		} else {
			this.getRoom()
			this.getRoomList()
		}
	},
	// 转化毫秒时间戳为日期格式
	filters: {
		getYMD: function(value) {
			return moment(value).format('YYYY-MM-DD')
		},
		getHm: function(value) {
			return moment(value).format('HH:mm')
		},
		getDay: function(value) {
			return moment(value).format('E')
		},
		getLength: function(value) {
			return value.length
		}
	},
	methods: {

		conClick(e) {
			this.$refs.clickCircle.conClick(e);
		},
		toTop() {
			this.toView = ''
			setTimeout(() => {
				this.toView = 'top' + this.currentTab
			}, 10)
		},
		// 判断登录
		isLogin() {
			this.userInfo = global.isLogin();
			console.log(this.userInfo)
			if (!this.userInfo) {
				uni.showModal({
					title: "请先登录",
					showCancel: false,
					success: function(res) {
						if (res.confirm) {
							console.log("确定了啊")
							uni.switchTab({
								url: "/pages/profile/profile"
							})
						}

					}
				})
			}
		},
		getRoom() {
			var that = this;
			uni.request({
				url: "https://api.lifeisgg.online/attendance/home/home",
				data: {
					"uId": that.userInfo.uId,
					"rId": "",
					"state": "",
					"lDate": ""
				},
				success(res) {
					console.log(res.data)
					that.roomLog = res.data.roomLog
					if (that.roomLog) {
						uni.showToast({
							title: "欢迎进入" + that.roomLog[0].rName,
							icon: "none"
						})
					}

				}
			})
		},
		PickerChange(e) {
			this.index = e.detail
			console.log(e.detail.value)
			this.chooseRoom = this.pickRoomList[e.detail.value].rId
			console.log(this.chooseRoom)
			this.entryRoom()
		},
		getRoomList() {
			var that = this;
			uni.request({
				url: "https://api.lifeisgg.online/attendance/room/room",
				success(res) {
					that.pickRoomList = res.data.roomList
					console.log(that.pickRoomList)
				}
			})

		},
		getMsg(e) {
			console.log("e=" + e.date)
			wx.cloud.callFunction({
				name: 'sendMsg',
				data: {
					"info": e
				},
				success(res) {
					console.log(res);
					wx.showToast({
						title: '发送成功!',
					})

				},
				fail(err) {
					console.log(err);
					wx.showToast({
						title: '发送失败!',
					})
				}
			})
		},
		sendMsg: function(e) {
		    wx.requestSubscribeMessage({
		      tmplIds: [
		        'jxm2u-gMXptrgJBsHOThe9-1L4Vw2dghG7J05FLEEKk',
		      ],
		      success(res) {
		        console.log(res)
		      },
		      fail(err) {
		        console.log(err);
		      }
		    })
		  },
		entryRoom() {
			var that = this;
			let now = moment().format('YYYY-MM-DD HH:mm:ss')
			uni.request({
				url: "https://api.lifeisgg.online/attendance/home/home",
				data: {
					"uId": that.userInfo.uId,
					"rId": that.chooseRoom,
					"state": 1,
					"lDate": now
				},
				success(res) {
					console.log(res.data)
					that.roomLog = res.data.roomLog

					that.MsgInfo.nickName = that.userInfo.nickName
					that.MsgInfo.title = "进入" 
					that.MsgInfo.address = that.roomLog[0].address
					that.MsgInfo.remind = "无"
					that.MsgInfo.date = now
					console.log("info" + that.MsgInfo.date)
					that.sendMsg()
					that.getMsg(that.MsgInfo)

				}
			})
		},
		exitRoom() {
			var that = this;
			uni.showModal({
				title: "确定要退出吗",
				success: function(res) {
					if (res.confirm) {
						let now = moment().format('YYYY-MM-DD HH:mm:ss')
						let rName = that.roomLog[0].rName
						let address = that.roomLog[0].address
						let rId = that.roomLog[0].rId
						uni.request({
							url: "https://api.lifeisgg.online/attendance/home/home",
							data: {
								"uId": that.userInfo.uId,
								"rId": rId,
								"state": 0,
								"lDate": now
							},
							success(res) {
								console.log(res.data.success)
								that.roomLog = res.data.roomLog

								that.MsgInfo.nickName = that.userInfo.nickName
								that.MsgInfo.title = "退出"
								that.MsgInfo.address = address
								that.MsgInfo.remind = "无"
								that.MsgInfo.date = now
								console.log("info" + that.MsgInfo.date)
								that.sendMsg()
								that.getMsg(that.MsgInfo)

							}
						})

					}


				}
			})


		}
	}
}
